
@use "../base/variable.scss" as *;
@use "../abstract/color.scss" as *;

.navigation {
    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: space-between;
    align-items: center;

    height: 64px;
    width: 100%;
    background-color: $neutral-100;
    // padding: 12px 24px;
    z-index: 999;
    box-shadow: 
        0 1px 2px 0 rgba(0, 0, 0, 0.03), 
        0 1px 6px -1px rgba(0, 0, 0, 0.02), 
        0 2px 4px 0 rgba(0, 0, 0, 0.02);

    &.row {
      gap: 2px;  
    }
    .navigation-logo {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: flex-end;
        align-items: center;
        gap: 20px;
        height: 24px;
        width: auto;
    }
    .navigation-links {
        display: flex;
        gap: $base-gap;
        padding: 0 48px 0 0;
        justify-content: flex-end;
        a {
            text-decoration: none;
        }
        
        
    }
}